* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-top: 32px;
  margin-bottom: 16px;
  margin-left: 32px;
}

img {
  display: block;
}

.country-list {
  margin-left: 40px;
}

.country-info {
  margin-left: 40px;
}

.card-title {
  display: flex;
  align-items: center
  /* width: 30px;
  height: 20px; */
}

.country-name {
  margin-left: 20px;
}